@import url(https://fonts.googleapis.com/css2?family=Space+Mono&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background-color: hsl(185, 41%, 84%);
  font-family: 'Space Mono', monospace;
  font-size: 24px;

}
*{
  border-radius: 10px;
}

.container{
  display: grid;
  grid-template-columns: 1fr 1fr;
  position: absolute;
  top: 50%;
  left:50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background-color: hsl(0, 0%, 100%);
}

.leftRightContainer {
  background-color: orange;
}
.containerRight{
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center;

}
.buttonRight{
    width: 100%;
    background-color: rgba(13,104,109,255);
    color:hsl(183, 100%, 15%);
    border: none;
    border-radius: 12px;
    margin: 85px 0 0;
    text-transform: uppercase;
}
.buttonRight:hover{
    background-color: hsl(185, 41%, 84%);
}
.background{
    background-color: hsl(183, 100%, 15%);
    padding: 1rem;
    margin: 1rem;
}

p{
    color:hsl(172, 67%, 45%);
    font-size: 30px;
    margin: 0
}
span{
    color: hsl(0, 0%, 57%);
    font-size: 12px
}
h6{
    color: hsl(0, 0%, 100%);
    margin: 0;
}

.gridTip{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr ;
}

button{

    font-family: 'Space Mono', monospace;
    font-size: 20px;
    margin: 10px 10px 10px;
    width: 85%;
    color: #f4fafa;
    background-color: #00494d;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}
button:hover{
    background-color: hsl(185, 41%, 84%);
    color: #00494d;
    font-weight: 700;
}
/*button:active{*/
/*    background-color: hsl(172, 67%, 45%);*/
/*}*/

.buttonTip{
    background-color: hsl(172, 67%, 45%);
    color: #00494d;
    font-weight: 700;
}

#custom{
    font-family: 'Space Mono', monospace;
    font-size: 18px;
    padding: 0 0 4px;
    text-align: center;
    margin: 12px 0 0 8px;
    width: 100%;
    cursor: pointer;
}
#custom:hover{

}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;

}
input{
    direction: rtl;
    padding: 0;
    border: none;
    width: 100%;
    background-color: hsl(189, 41%, 97%);
    font-family: 'Space Mono', monospace;
    font-size: 24px;

}
.regularTips:focus-visible{
    outline: 1px solid #26c0ab !important;
}

.error{
    color: red;
}
.customTips:focus-visible{
    outline: 1px solid #26c0ab !important;
}
.leftContainer{
    margin: 1.5rem;
}
h5{
    margin: 0;
    padding: 0;
}
